.series-bar-container {
  display: grid;
  grid-template-columns: 75px 1fr 75px;
  grid-template-rows: 1fr 75px 1fr; }

.series-bar-container__list {
  grid-column: 1 / span 3;
  grid-row: 1 / span 3;
  grid-template-columns: repeat(6, 1fr); }

.series-bar-container__button {
  z-index: 999999;
  overflow: hidden;
  position: relative; }
  .series-bar-container__button img {
    opacity: .5; }

.series-bar-container__button--active {
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; }

.series-bar-container__button--prev,
.series-bar-container__button--next {
  grid-column: 1;
  grid-row: 2;
  -webkit-animation-name: arrow-animation-left;
          animation-name: arrow-animation-left; }

.series-bar-container__button--next {
  grid-column: 3;
  -webkit-animation-name: arrow-animation-right;
          animation-name: arrow-animation-right; }

@-webkit-keyframes arrow-animation-right {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  50% {
    -webkit-transform: translateX(8px);
            transform: translateX(8px); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@keyframes arrow-animation-right {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  50% {
    -webkit-transform: translateX(8px);
            transform: translateX(8px); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@-webkit-keyframes arrow-animation-left {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  50% {
    -webkit-transform: translateX(-8px);
            transform: translateX(-8px); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@keyframes arrow-animation-left {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  50% {
    -webkit-transform: translateX(-8px);
            transform: translateX(-8px); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }



